import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-count-down-timer',
  templateUrl: './count-down-timer.component.html',
  styleUrls: ['./count-down-timer.component.scss']
})
export class CountDownTimerComponent implements OnInit {
  // Warning occurs at 10s
  WARNING_THRESHOLD = 5;
  // Alert occurs at 5s
  ALERT_THRESHOLD = 3;
  FULL_DASH_ARRAY = 283;


  COLOR_CODES = {
    info: {
      color: 'green'
    },
    warning: {
      color: 'orange',
      threshold: this.WARNING_THRESHOLD
    },
    alert: {
      color: 'red',
      threshold: this.ALERT_THRESHOLD
    }
  };


  TIME_LIMIT = 10;
  timePassed = 0;
  timeLeft = this.TIME_LIMIT;
  timerInterval = null;
  remainingPathColor = this.COLOR_CODES.info.color;

  constructor() { }

  ngOnInit(): void {
    this.startTimer();
  }


  setRemainingPathColor(timeLeft): void{
    const { alert, warning, info } = this.COLOR_CODES;

    // If the remaining time is less than or equal to 5, remove the "warning" class and apply the "alert" class.
    if (timeLeft <= alert.threshold) {
      document
        .getElementById('base-timer-path-remaining')
        .classList.remove(warning.color);
      document
        .getElementById('base-timer-path-remaining')
        .classList.add(alert.color);
    // If the remaining time is less than or equal to 10, remove the base color and apply the "warning" class.
    } else if (timeLeft <= warning.threshold) {
      document
        .getElementById('base-timer-path-remaining')
        .classList.remove(info.color);
      document
        .getElementById('base-timer-path-remaining')
        .classList.add(warning.color);
    }
  }


  formatTime(time): string {
    const minutes = Math.floor(time / 60);
    const seconds = time % 60;
    let secondsStr = seconds.toString();
    if (seconds < 10) {
      secondsStr = `0${seconds}`;
    }
    // return `${minutes}:${secondsStr}`;
    return `${secondsStr}`;

  }




calculateTimeFraction(): number {
  const rawTimeFraction = this.timeLeft / this.TIME_LIMIT;
  return rawTimeFraction - (1 / this.TIME_LIMIT) * (1 - rawTimeFraction);
}

setCircleDasharray() {
  const circleDasharray = `${(
    this.calculateTimeFraction() * this.FULL_DASH_ARRAY
  ).toFixed(0)} 283`;
  document
    .getElementById('base-timer-path-remaining')
    .setAttribute('stroke-dasharray', circleDasharray);
}


beginPractise(){
  clearInterval(this.timerInterval);
  this.timeLeft = this.TIME_LIMIT;
  this.timePassed = 0;
  this.formatTime(0);
  this.startTimer();
  this.remainingPathColor = this.COLOR_CODES.info.color;
}



onTimesUp() {
  clearInterval(this.timerInterval);
}

startTimer() {
  this.timerInterval = setInterval(() => {
    this.timePassed = this.timePassed += 1;
    this.timeLeft = this.TIME_LIMIT - this.timePassed;
    document.getElementById('base-timer-label').innerHTML = this.formatTime(
      this.timeLeft
    );
    this.setCircleDasharray();
    this.setRemainingPathColor(this.timeLeft);

    if (this.timeLeft === 0) {
      this.onTimesUp();
    }
  }, 1000);
}

}
