import { Component, OnInit } from '@angular/core';
import { VoiceActivity } from '../models/voice-activity';
import { ActivitiesService } from '../services/activities.service';
import { Router, ActivatedRoute, ParamMap, NavigationEnd } from '@angular/router';
import { DomSanitizer, SafeUrl } from '@angular/platform-browser';

import { Observable } from 'rxjs';
import { switchMap } from 'rxjs/operators';
import { NULL_EXPR } from '@angular/compiler/src/output/output_ast';
// tslint:disable-next-line:ban-types
declare let gtag: Function;

@Component({
  selector: 'app-activity-card',
  templateUrl: './activity-card.component.html',
  styleUrls: ['./activity-card.component.scss'],
  providers: [ActivitiesService]
})
export class ActivityCardComponent implements OnInit {
  activity: VoiceActivity;
  selectedId: number;
  maxItems: number;

  constructor(private activitiesService: ActivitiesService,
    private route: ActivatedRoute,
    private router: Router,
    private sanitizer: DomSanitizer) {

  }

  ngOnInit(): void {
    this.route.params.subscribe(routeParams => {
      this.selectedId = Number(routeParams.id);
      this.maxItems = this.activitiesService.getActivityCount();
      this.activity = this.activitiesService.getActivityById(this.selectedId);

      gtag('event', 'page_view', {
        page_title: 'Acivity/' + this.selectedId,
        page_path: '/activitylist/' + this.selectedId,
        send_to: 'G-89L1L09TTY'
      });
    });
  }


  previousActivity() {
    if (this.selectedId > 1) {
      this.router.navigate(['/activities', this.selectedId - 1]);
    }
  }

  nextActivity() {
    if (this.selectedId < this.maxItems) {
      this.router.navigate(['/activities', this.selectedId + 1]);
    }
  }

  backToList() {
    this.router.navigate(['/activities']);
  }

  safeUrl(url) {
    return this.sanitizer.bypassSecurityTrustResourceUrl(url);
  }

}
