import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject, Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { environment } from '@environments/environment';
import { User } from '@app/models';

@Injectable({ providedIn: 'root' })
export class AuthenticationService {
    private userSubject: BehaviorSubject<User>;
    public user: Observable<User>;

    private refreshTokenTimeout;

    constructor(
        private router: Router,
        private http: HttpClient
    ) {
        this.userSubject = new BehaviorSubject<User>(null);
        this.user = this.userSubject.asObservable();
    }

    public get userValue(): User {
        return this.userSubject.value;
    }

    login(username: string, password: string) {
        return this.http.post<any>(`${environment.apiUrl}/login`, { username, password }, { withCredentials: false })
            .pipe(map(user => {
                this.userSubject.next(user);
                this.startRefreshTokenTimer();
                this.setSession(user);
                return user;
            }));
    }

    private setSession(user){
        localStorage.setItem('token', user.jwtToken);
        localStorage.setItem('token2', user.refreshToken);
    }

    private clearSession(){
        localStorage.removeItem('token');
        localStorage.removeItem('token2');
    }

    logout() {
        this.http.post<any>(`${environment.apiUrl}/revoke-token`, {}, { withCredentials: false }).subscribe();
        this.stopRefreshTokenTimer();
        this.userSubject.next(null);
        this.clearSession();
        this.router.navigate(['/login']);
    }

    refreshToken() {
        const refreshToken = localStorage.getItem('token2');
        return this.http.post<any>(`${environment.apiUrl}/refresh-token?refreshToken=${refreshToken}`, {}, { withCredentials: false })
            .pipe(map((user) => {
                this.userSubject.next(user);
                this.setSession(user);
                this.startRefreshTokenTimer();
                return user;
            }));
    }

    // helper methods


    private startRefreshTokenTimer() {
        // parse json object from base64 encoded jwt token
        const jwtToken = JSON.parse(atob(this.userValue.jwtToken.split('.')[1]));

        // set a timeout to refresh the token a minute before it expires
        const expires = new Date(jwtToken.exp * 1000);
        const timeout = expires.getTime() - Date.now() - (60 * 1000);
        this.refreshTokenTimeout = setTimeout(() => this.refreshToken().subscribe(), timeout);
    }

    private stopRefreshTokenTimer() {
        clearTimeout(this.refreshTokenTimeout);
    }
}
