import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { HttpHeaders } from '@angular/common/http';


import { Observable } from 'rxjs';
import { catchError } from 'rxjs/operators';

import {environment} from '../../environments/environment';

const httpOptions = {
    headers: new HttpHeaders({
      'Content-Type':  'application/json',
      Authorization: 'my-auth-token'
    })
  };

@Injectable()
export class RecorderService {

  recordurl = `${environment.apiBaseUrl}/update`;  // URL to web api
  constructor(
    private http: HttpClient) {
  }
  //////// Save methods //////////

  /** POST: add a new recording to the database */
  sendRecord(body: any): Observable<any> {
    console.log('Got ');
    console.log(body);
    const formData = new FormData();
    formData.append('file', body.file.blob, body.filename );
    return this.http.post<any>(this.recordurl, formData);
  }

}
