import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { CountDownTimerComponent } from '../count-down-timer/count-down-timer.component';
import { AudioRecordingService } from '../services/audio-recording.service';
import { RecorderService } from './recorder.service';

@Component({
  selector: 'app-recorder',
  templateUrl: './recorder.component.html',
  styleUrls: ['./recorder.component.scss'],
  providers: [RecorderService],

})
export class RecorderComponent implements OnInit, OnDestroy {
  downCounter: CountDownTimerComponent;

  @ViewChild('downCounter', {static: false}) set content(content: CountDownTimerComponent){
    if (content){
      this.downCounter = content;
    }
  }
  recording = false;
  recordingCaption = 'Record';
  isRecording = false;
  recordedTime: any;
  blobUrl: any;


  constructor(private audioRecordingService: AudioRecordingService,
              private recorderService: RecorderService,
              private sanitizer: DomSanitizer
              ) {
    this.audioRecordingService.recordingFailed().subscribe(() => {
      this.isRecording = false;
    });

    this.audioRecordingService.getRecordedTime().subscribe((time) => {
      this.recordedTime = time;
    });

    this.audioRecordingService.getRecordedBlob().subscribe((data) => {
      this.blobUrl = this.sanitizer.bypassSecurityTrustUrl(URL.createObjectURL(data.blob));

      recorderService.sendRecord({item: 'Recorded', file: data, filename: this.blobUrl}).subscribe({
        next: dt => {
            console.log('Hit the server');
            console.log(dt);
        },
        error: error => {

            console.error('There was an error!', error);
        }
    });
      console.log('Send recording');
    });

    }

  ngOnInit(): void {
  }


  startRecording() {
    if (!this.isRecording) {
      this.isRecording = true;
      this.audioRecordingService.startRecording();

      if (this.downCounter){
        this.downCounter.beginPractise();
      }
    }
  }

  abortRecording() {
    if (this.isRecording) {
      this.isRecording = false;
      this.audioRecordingService.abortRecording();
      this.downCounter.onTimesUp();

    }
  }

  stopRecording() {
    if (this.isRecording) {
      this.audioRecordingService.stopRecording();
      this.isRecording = false;
      this.downCounter.onTimesUp();

    }
  }

  clearRecordedData() {
    this.blobUrl = null;
  }

  ngOnDestroy(): void {
    this.abortRecording();

    if (this.downCounter){
      this.downCounter.onTimesUp();    }
  }


}
