import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';
import { VoiceActivity } from '../models/voice-activity';
import { ActivitiesService } from '../services/activities.service';
// tslint:disable-next-line:ban-types
declare let gtag: Function;

export interface Section {
  name: string;
  updated: Date;
}

@Component({
  selector: 'app-activity-list',
  templateUrl: './activity-list.component.html',
  styleUrls: ['./activity-list.component.scss'],
  providers: [ActivitiesService]
})
export class ActivityListComponent implements OnInit {

  activities: VoiceActivity[];


  constructor(private activitiesService: ActivitiesService,
              private route: ActivatedRoute,
              private router: Router) {
    this.activities = [];

    this.router.events.subscribe(event => {
      if (event instanceof NavigationEnd){
        gtag('event', 'page_view', {
          page_title: 'Acivity List',
          page_location: event.urlAfterRedirects,
          page_path: '/activitylist',
          send_to: 'G-89L1L09TTY'
        });
       }
    }
   );
  }

  ngOnInit(): void {
    this.activities = this.activitiesService.getAllActivitites();
    // this.activitiesService.getAllActivitites();
  }

  clickActivity(act: VoiceActivity){
    this.router.navigate(['/activities/', act.id]);
  }

}
