import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { VoiceActivity } from '../models/voice-activity';
import * as data from './activity-list.json';

@Injectable()
export class ActivitiesService {

  constructor() {

  }

  getAllActivitites(): VoiceActivity[]{
    const activities = (data as any).default;

    console.log(activities);
    return activities;
  }

  getActivityById(id: number): VoiceActivity{
    console.log('looking for '+ id);

    const activities = (data as any).default as VoiceActivity[];
    let selectedElement: VoiceActivity;
    activities.forEach(element => {
      if (element.id === id){

        selectedElement = element;
      }
    });

    console.log("Found..");
    console.log(selectedElement);
    return selectedElement;

  }

  getActivityCount(): number {
    return (data as any).default.length;
  }
}

